import React,{useState,useEffect} from 'react'
import Card from '@material-ui/core/Card';
import { makeStyles,withStyles } from '@material-ui/core/styles';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import './Detail.css'
import Button from '@material-ui/core/Button';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import NativeSelect from '@material-ui/core/NativeSelect';
import InputBase from '@material-ui/core/InputBase';
import EmployeeService from './EmployeeService'
import {useHistory} from 'react-router-dom';
import Validation from './Validation';
import CancelIcon from '@material-ui/icons/Cancel';
import CheckCircleIcon from '@material-ui/icons/CheckCircle';
import Input from '@material-ui/core/Input';
import Swal from 'sweetalert2'
import { useParams } from 'react-router-dom';
import FooterComponent from './FooterComponent.js';


const useStyles = makeStyles({
  root: {
    minWidth: 270,
    height: 'auto',
     border: '2px solid black',
     filter: 'drop-shadow(0 0 0.75rem grey)'
  },
 
  title: {
    fontSize: 34,
    forWeight: 'bold'
  },
  pos: {
    marginBottom: 12,
  },
  date: {
  	border: '2px solid red'
  },
  date1: {
  	border: '2px solid green'
  },
});

const BootstrapInput = withStyles((theme) => ({
  root: {
    'label + &': {
      marginTop: theme.spacing(3),
    },
  },
  input: {
    borderRadius: 4,
    position: 'relative',
    backgroundColor: theme.palette.background.paper,
    border: '1px solid #ced4da',
    fontSize: 16,
    padding: '10px 26px 10px 12px',
    transition: theme.transitions.create(['border-color', 'box-shadow']),
    // Use the system font instead of the default Roboto font.
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(','),
    '&:focus': {
      borderRadius: 4,
      borderColor: '#80bdff',
      boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
    },
  },
}))(InputBase);

const UpdateEmployee=(props)=>{

	const {id} = useParams();

	const classes = useStyles();
	

	// const[firstname,setName]=useState("");
	// const[lastname,setLastName]=useState("");
	// const[date,setDate]=useState("");
	// const[gender,setGender]=useState("");
	// const[address,setAddress]=useState("");
	// const[phone,setPhone]=useState("");
	// const[emailid,setEmailId]=useState("");
	// const [position, setPosition] = useState("");
  
  	const[employees,setEmployees]=useState({
  		firstName:'',
  		lastName:'',
  		dateOfBirth:'',
  		gender:'',
  		address:'',
  		phoneNumber:'',
  		emailId:'',
  		positionTitle:''
  	});	

  	const[error,setError]=useState("");											//first name
  	const[errorVal,setErrorVal]=useState(false);

  	const[error1,setError1]=useState("");										//last name
  	const[errorvallast,setErrorValLast]=useState(false);

  	const[phoneerr,setPhoneErr]=useState("");									//phone error
  	const[errorVal1,setErrorVal1]=useState(false);

  	const[emailerr,setEmailErr]=useState(false);								//email error
  	const[errtext,setErrorText]=useState("");
  	const[text,setText]=useState("");

  	const[selecterr,setSelectErr]=useState("");		
  	const[selecttxt,setSelectTxt]=useState(false);								//option error
  	
  	const[dateerr,setDateErr]=useState("");										//date error

  	const Swal = require('sweetalert2')

	 useEffect(()=>{
	  	EmployeeService.getEmployeeById(id).then((res)=>{
	  
	  		setEmployees(res.data);
	  		// console.log(res);
	  		})
	  	},[id])

  	console.log("the firstname: ",employees.firstname);
  // const handleChange = (event) => {
  //   setPosition(event.target.value);
  // };

	let history=useHistory();
	const handleUpdate=()=>
	{
		let employeeDetail={firstName:employees.firstName,lastName:employees.lastName,dateOfBirth:employees.dateOfBirth,gender:employees.gender,address:employees.address,phoneNumber:employees.phoneNumber,emailId:employees.emailId,positionTitle:employees.positionTitle};
		console.log(JSON.stringify(employeeDetail));

		if(((employees.firstName==="") && setErrorVal(true)) || ((employees.lastName==="") && setErrorValLast(true)) || (employees.dateOfBirth==="") || (employees.gender==="") || ((employees.phoneNumber==="")&&(setErrorVal1(true))) || ((employees.emailId==="")&&setEmailErr(true)) || (employees.positionTitle==="") )
		{
			
			Swal.fire({
			  icon: 'error',
			  title: 'Oops...',
			  text: 'Some data missing!',
			})
		}
		if(((employees.phoneNumber).toString().length!=10) || (text==="fail"))
		{
			Swal.fire({
			  icon: 'error',
			  title: 'Oops...',
			  text: 'Some data missing!',
			})
		}
		if(employees.positionTitle=="")
		{
			Swal.fire({
			  icon: 'error',
			  title: 'Oops...',
			  text: 'Some data missing!',
			})
		}
		else
		{
			EmployeeService.updateEmployee(id,employeeDetail).then((res)=>{
			// 	Swal.fire({
			//   icon: 'success',
			//   title: 'saved to DB',
			//   text: 'move to employees list',
		 	  
			// }).then(function() {
   //  				history.push('/employees')	
			// 	});
			// })

			Swal.fire({
				  title: 'Do you want to save the changes?',
				  showDenyButton: true,
				  confirmButtonText: `Save`,
				  denyButtonText: `Don't save`,
				}).then((result) => {
				  /* Read more about isConfirmed, isDenied below */
				  if (result.isConfirmed) {
				    Swal.fire('Saved!', '', 'success').then(function(){history.push('/employees')})
				  } else if (result.isDenied) {
				    Swal.fire('Changes are not saved', '', 'info')
				  }
				})
			});
			setErrorVal(false)
			setErrorValLast(false)
			
		}
}
	// const handleAddress=(event)=>
	// {
		
	// 	setAddress(event.target.value)
	// }
	
	const handleCancel=()=>{
		history.push("/employees");
	}

		const handleBlur=()=>{
		if(employees.firstName=="")
		{	
			setErrorVal(true)
			setError("This is required")
		}
		else{setErrorVal(false); setError("")}
		}
		
	const handleBlurLastName=()=>
	{
		if(employees.lastName=="")
		{
			setErrorValLast(true)
			setError1("This is required!")
		}
		else
		{
			setErrorValLast(false)
			setError1("")
		}
	}

	const handleBlurPhone=()=>
	{
		const phonenum=new RegExp(/^\(?([0-9]{3})\)?[-. ]?([0-9]{3})[-. ]?([0-9]{4})$/);
		var phone=employees.phoneNumber;
		if(phone.toString().match(phonenum))
		{
			setErrorVal1(false);
			setPhoneErr("")
		}
		else
		{
			setErrorVal1(true)
			setPhoneErr("phoneNumber must be == 10.")
		}
	}

	const handleBlurEmail=()=>{

		const email=new RegExp(/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/);
		
		if((employees.emailId).match(email))
		{
			setEmailErr(false)
			setErrorText("")
			setText("sucess");
		}
		else
		{
			setEmailErr(true)
			setText("fail")
			setErrorText("emailId must be following (a-z),(0-9)")
		}
	}

	const handleBlurSelect=()=>
	{
		if(employees.positionTitle==="")
		{
			setSelectErr("must select one option!")
			setSelectTxt(true)
		}
		else
		{
			setSelectTxt(false)
			setSelectErr("")
		}
	}

	const handleBlurDate=()=>{

		let d=new Date(employees.dateOfBirth)
		let d1=d.getMonth()+1;
		let d2=d.getDate();
		let d3=d.getFullYear();
		if(isNaN(d1) || isNaN(d2) || isNaN(d3))
		{
			setDateErr(classes.date);

		}
		else
		{
			setDateErr(classes.date1);
		}
	}

	const onChange1=(e)=>
	{
		// setEmployees({[e.target.name]:e.target.value})
		setEmployees({...employees,[e.target.name]:e.target.value})
		console.log("the value id:"+e.target.name);
		console.log("the employee are:",employees);
	}


	return(
		<div className="form2">

		<Card className={classes.root}>
		<CardContent>
			<Typography className={classes.title}  gutterBottom>
	          UpdateEmployee
	        </Typography>
	        <div className="form1">
		        <label className="txt1">First Name
		        <div className="field1">    	
		        	 <TextField className="txtfield1"  error={errorVal} helperText={error} name="firstName" onBlur={handleBlur} onChange={(e)=>{onChange1(e)}} value={employees.firstName.toUpperCase()} size="small"  label="First Name" variant="outlined" required />
		        </div>
		        </label>
		       <br/>

		       <label className="txt1">Last Name
		        	<TextField className="txtfield2" error={errorvallast} helperText={error1} onBlur={handleBlurLastName} name="lastName" onChange={(e)=>{onChange1(e)}} value={employees.lastName.toUpperCase()} size="small"  label="Last Name" variant="outlined" />
		        </label>
		        <br/>

		        <label className="txt1">Date Of Birth
		        <div className="field2">
		        	 <input onBlur={handleBlurDate} onChange={(e)=>{onChange1(e)}}  type="date" name="dateOfBirth" 
					        placeholder="dd-mm-yyyy" value={employees.dateOfBirth}
					        min="1997-01-01" max="2030-12-31"
					 />{isNaN(employees.dateOfBirth) ? <CheckCircleIcon style={{color:'green'}} /> : <CancelIcon style={{color:'red'}} />}
					
		        </div>
		        </label>
		        <br/>

		        <label className="txt">Gender
		        <div className="radio1">
		        	 <RadioGroup className="radio" onChange={(e)=>{onChange1(e)}} aria-label="gender" name="gender" value={employees.gender} >
				        <FormControlLabel value="female" control={<Radio />} label="Female" />
				        <FormControlLabel value="male" control={<Radio />} label="Male" />
				        <FormControlLabel value="other" control={<Radio />} label="Other" />
				     
				      </RadioGroup>

		        </div>
		        </label>
		        <br/>

		        <label className="txt1">Address &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
		        <div>
		        	<TextField onChange={(e)=>{onChange1(e)}} name="address" value={employees.address} size="small" className={classes.input} label="Address Line-1" variant="outlined" required/>       	
		        </div>
		       </label>
		        <br/>

		        <label  className="txt1">Phone Number
		        <div>
					<TextField className="phone" error={errorVal1} helperText={phoneerr} onBlur={handleBlurPhone} onChange={(e)=>{onChange1(e)}} name="phoneNumber" type="number" value={employees.phoneNumber} placeholder="XXX-XXX-XXXX" label="Phone Number" size="small" variant="outlined" required/>{((employees.phoneNumber).toString().length===10) ? <CheckCircleIcon style={{color:'green'}} /> : <CancelIcon style={{color:'red'}} />}
		        </div>
		        </label>
		        <br/>

		        <label className="txt1">Email Id
		        <div>
		        	<TextField className="emailid" error={emailerr} helperText={errtext} onBlur={handleBlurEmail} onChange={(e)=>{onChange1(e)}} name="emailId" label="Email Id" placeholder="ex:abc@gmail.com" value={employees.emailId.toLowerCase()} size="small" variant="outlined" required/>{((text ==="sucess") && <CheckCircleIcon style={{color:'green',position:'relative',left:'53px',top:'5px'}} />) || ((text==="fail") && <CancelIcon style={{color:'red'}} />) }
		        	
		        </div>
		        </label>
		        <br/>

		        <label className="txt1">Position title
		        <div className="select">
		        		<NativeSelect
				          id="demo-customized-select-native"
				          value={employees.positionTitle}
				          input={<BootstrapInput />}
				          name="positionTitle"
				          onChange={(e)=>{onChange1(e)}}
				          onBlur={handleBlurSelect}
				          error={selecttxt} 
				          helperText={selecterr}
				        >
				          <option label="Select Title" value="" />
				          <option  value="Android App Development">Android App Development</option>
				          <option  value="Java Developer">Java Developer</option>
				          <option  value="AngularJs Developer">Angular Js</option>
				          <option  value="Fresher">Fresher</option>
				           <option value="Web Development">Web Development</option>
				        </NativeSelect>{(employees.positionTitle==="") ? <CancelIcon style={{color:'red'}} /> : <CheckCircleIcon style={{color:'green'}} />}
		        </div>
		        </label>
		        <br/>

		        <div className="btn">
		        	<Button onClick={handleUpdate} variant="contained" color="primary">
					  Save
					</Button>
		   
		        	<Button onClick={handleCancel} variant="contained" color="secondary">
					  Cancel
					</Button>
		        </div>

	        </div>
		</CardContent>
		</Card>
		 <FooterComponent />
		</div>
		)
}
export default UpdateEmployee;